
// Global styling for this template
body,
html {
  width: 100%;
  min-height: 100vh;
  overflow-x: hidden;
}

body {
  @include serif-font;
}

hr {
  max-width: 50px;
  border-width: 3px;
  border-color: $primary;
}

hr.light {
  border-color: $white;
}

.colum {
  border-width: 3px;
  border-right: solid 0.2px $menu-color;    
}

a {
  color: $primary;
  @include transition-all;
  &:hover {
    color: darken($primary, .1);
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  @include sans-serif-font;
}

.bg-primary {
  background-color: $primary !important;
}

.bg-dark {
  background-color: $gray-900 !important;
}

.text-faded {
  color: $white;
}

section {
  padding: 8rem 0;
}

.section-heading {
  margin-top: 0;
}

::-moz-selection {
  color: $white;
  background: $gray-900;
  text-shadow: none;
}

::selection {
  color: $white;
  background: $gray-900;
  text-shadow: none;
}

img::selection {
  color: $white;
  background: transparent;
}

img::-moz-selection {
  color: $white;
  background: transparent;
}

body {
  -webkit-tap-highlight-color: $gray-900;
}

.btn-green{
    background-color: $green-btn;
    color: $white !important;
}

.icon-medium{
    font-size: 25px;
}

.icon-big {
    font-size: 70px;
}

.border-white{
    border: 1px solid #fff;
}

// utils

.no-padding {
  padding: 0;
}
.no-margin {
  margin: 0;
}

.text-secondary {
  font-weight:500;
  color: $secondary !important;
}

